@import url(https://fonts.googleapis.com/css?family=Abel&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

p, h1, h2, h3, h4, h5, h6, span, button, a {
  font-family: 'Abel', sans-serif;
  font-weight: bolder !important; }

.listresult {
  display: none; }

.imgareaselect-border1, .imgareaselect-border2,
.imgareaselect-border3, .imgareaselect-border4 {
  filter: alpha(opacity=50);
  opacity: 0.1;
  background-color: orange;
  position: relative; }
  .imgareaselect-border1::after, .imgareaselect-border2::after,
  .imgareaselect-border3::after, .imgareaselect-border4::after {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    content: "99";
    color: #000; }

.imgareaselect-border1 {
  border: solid 2px orangered; }

.imgareaselect-border2 {
  border: solid 2px orangered; }

.imgareaselect-handle {
  background-color: #fff;
  border: solid 1px #000;
  filter: alpha(opacity=50);
  opacity: 0.5; }
  .imgareaselect-handle::after {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    content: "99";
    color: #000; }

.imgareaselect-outer {
  background-color: #000;
  filter: alpha(opacity=40);
  opacity: 0.4; }

.imgareaselect-selection {
  background-color: #fff;
  filter: alpha(opacity=0);
  opacity: 0; }
  .imgareaselect-selection::after {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    content: "99";
    color: #000; }

#homepage {
  height: 100vh; }
  #homepage .ai {
    margin: 0;
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%); }
    #homepage .ai-image {
      width: 100%; }
    #homepage .ai-box {
      margin-top: 3vw; }
      #homepage .ai-box-title {
        font-family: 'Abel', sans-serif; }
      #homepage .ai-box-list form {
        display: flex;
        justify-content: space-around; }
        #homepage .ai-box-list form .selected {
          cursor: pointer;
          z-index: 2;
          position: relative;
          padding: .5vw 1vw;
          border-radius: 20px;
          background-color: #DDDBDD;
          border: none;
          color: #000000; }
          #homepage .ai-box-list form .selected:hover {
            background-color: orange;
            color: #ffffff;
            -webkit-transition: .2s;
            transition: .2s; }
            #homepage .ai-box-list form .selected:hover a {
              color: #ffffff; }
          #homepage .ai-box-list form .selected a {
            text-decoration: none;
            color: #000000; }
      #homepage .ai-box-steps {
        display: flex;
        justify-content: flex-start;
        margin: 3vw 2vw; }
        #homepage .ai-box-steps .complete {
          margin-right: 1vw;
          background: #56535694;
          color: #ffffff;
          padding: .5vw 1vw;
          border-radius: 20px;
          font-family: 'Abel', sans-serif;
          font-weight: bolder !important;
          border: none; }
          #homepage .ai-box-steps .complete:hover {
            background: #1053A7;
            -webkit-transition: .2s;
            transition: .2s; }
        #homepage .ai-box-steps .cancle {
          background-color: #ffffff;
          color: #56535694;
          border: 1px solid #56535694;
          border-radius: 20px;
          font-family: 'Abel', sans-serif;
          font-weight: bolder !important;
          padding: .5vw 1vw; }
          #homepage .ai-box-steps .cancle:hover {
            border: 1px solid #1053A7;
            color: #1053A7; }
      #homepage .ai-box-next {
        margin-left: 2vw; }
        #homepage .ai-box-next .skip {
          font-family: 'Abel', sans-serif;
          font-weight: bolder !important;
          background: none;
          border: none;
          color: #56535694; }

.active-orange {
  background-color: orange !important;
  color: #ffffff !important; }
  .active-orange a {
    color: #ffffff !important; }

